import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Message from './message/Message';

import './Dialogue.less';


export default class Dialogue extends Component {
  render() {
    const { transcript } = this.props;

    const content = transcript.map((item) => <Message key={item.phrase} data={item} />)

    return (
      <div className="dialogue-component">

        <header className="dialogue-component--header">
          <h2 className="dialogue-component--title">Пример звонка.wav</h2>
          <p className="dialogue-component--time">21 мар 18:03:41</p>
        </header>

        <div className="dialogue-component--container">
          {content}
        </div>

      </div>
    );
  }
};

Dialogue.propTypes = {
  transcript: PropTypes.arrayOf(PropTypes.object).isRequired
}